<template>
  <p>Main View, nigga!</p>
  <div id="map" style="width: 600px; height: 400px"></div>
  <input type="text" plac>
</template>

<script>
import axios from 'axios'
import YandexMap from '../mapScript'
export default {

  data () {
    return {
      login: '',
      password: '',
      error: false,
      coords: []
    }
  },
  mounted() {
    this.$nextTick(function () {
        ymaps.ready(function () {
          const myMap = new ymaps.Map('map', {
            center: [55.76, 37.64],
            zoom: 10
          });
          myMap.events.add('click', function (e) {
            if (!myMap.balloon.isOpen()) {
              this.coords = e.get('coords');
              myMap.balloon.open(this.coords, {
                contentHeader:'Photo was taken here!',
                contentBody:
                  '<p>Coordinates: ' + [
                    this.coords[0].toPrecision(6),
                    this.coords[1].toPrecision(6)
                  ]
              });
            }
            else {
              myMap.balloon.close();
            }
          })
        })
    })
  },
  methods: {
    LoginUser () {
      const user = JSON.stringify({ login: this.login, password: this.password })
      const customConfig = {
        headers: {
          'Content-Type': 'application/json'
        }
      }
      axios.post('api/account/signin', user, customConfig).then(response => {
        const temp = response.data
        VueCookies.set('token' , temp.data.token)
        this.$router.push('/main')
      }).catch((error) => {
        this.error = true
        console.log(error.response.status)
      })
    }
  }
}
</script>
